* {
  margin: 0;
  padding: 0; }

body, html {
  height: 100%; }

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 0px 0px;
  box-sizing: border-box;
  background-color: #d6e1fe; }

#source-button {
  min-width: 150px;
  min-height: 40px;
  margin: 20px;
  padding: 10px 20px;
  font-size: calc(10px + 6 * ((100vw - 320px) / 960));
  font-weight: bold;
  color: white;
  border: none;
  background-color: #6c9dd8;
  cursor: pointer; }

#sources {
  display: none;
  flex-direction: column;
  align-items: center;
  flex: 1;
  padding: 10px 50px;
  background-color: #ffffff; }

#sourcesroot {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: space-around; }

.info-container {
  display: flex;
  flex: 1;
  width: 100%; }

.resource {
  margin: 40px 0;
  font-size: 36px;
  font-size: calc(18px + 12 * ((100vw - 320px) / 960)); }

.source {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 150px;
  height: 30px;
  padding: 10px;
  margin: 20px;
  color: #000000;
  text-align: center;
  text-decoration: none;
  border-radius: 5px;
  cursor: pointer;
  overflow: hidden; }

.source:hover {
  background-color: #98beec;
  transform: scale(1.2, 1.2); }
