#preloadroot {
  position: absolute;
  width: 100%;
  height: 100%;
  display: none;
  justify-content: center;
  align-items: flex-start;
  padding-top: 100px;
  background-color: #d6e1fe;
  opacity: 0;
  animation: fadeIn 1s infinite; }

@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.loading-text {
  font-size: calc(14px + 12 * ((100vw - 320px) / 960));
  font-weight: bold;
  color: #316fb9; }
